import { useMemo } from "react";
import { SpecialZoom, ViewerHandlers } from "../core/types";
import { useAtom, useAtomValue, useSetAtom } from "jotai";
import {
  pageIndexAtom,
  pagesAtom,
  scaleAtom,
  specialZoomAtom,
  viewerElementAtom,
} from "../atoms";
import { getPagePositions, getSpecialZoomScale } from "../core/utils";

// external handlers
export const useViewerHandlers = () => {
  const viewerContainer = useAtomValue(viewerElementAtom);
  const pages = useAtomValue(pagesAtom);
  const pageIndex = useAtomValue(pageIndexAtom);
  const setSpecialZoom = useSetAtom(specialZoomAtom);
  const [scale, setScale] = useAtom(scaleAtom);

  const handlers: ViewerHandlers | undefined = useMemo(() => {
    if (!viewerContainer) return;
    const { pagePositions } = getPagePositions(pages, 10, scale);
    return {
      goToPage: (index: number) =>
        viewerContainer?.scrollTo({
          behavior: "smooth",
          left: 0,
          top: pagePositions[index].top,
        }),
      next: () =>
        viewerContainer?.scrollTo({
          behavior: "smooth",
          left: 0,
          top: pagePositions[pageIndex + 1].top,
        }),
      prev: () =>
        viewerContainer?.scrollTo({
          behavior: "smooth",
          left: 0,
          top: pagePositions[pageIndex - 1].top,
        }),
      zoomToSpecialValue: (scale) => {
        const realScale = getSpecialZoomScale(
          scale,
          viewerContainer,
          pages[0].width,
          pages[0].height
        );
        if (realScale) setScale(realScale);
        setSpecialZoom(scale);
      },
      zoom: (scale) => {
        setScale(scale);
        setSpecialZoom(SpecialZoom.None);
      },
      zoomIn: () => {
        setScale((scale) => scale + 0.1);
        setSpecialZoom(SpecialZoom.None);
      },
      zoomOut: () => {
        setScale((scale) => scale - 0.1);
        setSpecialZoom(SpecialZoom.None);
      },
    };
  }, [viewerContainer, pages, scale, pageIndex, setScale, setSpecialZoom]);

  return handlers;
};
