import { FC, useEffect, useMemo, useRef, useState } from "react";
import {
  InitialViewerState,
  PDFFile,
  Page,
  ViewerCallbacks,
} from "../../core/types";
import { pageIndexAtom, pagesAtom, scaleAtom } from "../../atoms";
import { useSetAtom } from "jotai";
import { createPdfWorker } from "../../core/worker";
import { useHydrateAtoms } from "jotai/utils";
import { VirtualList } from "./virtual-list";
import { PdfPage } from "./pdf-page";
import { SelectionContextMenu } from "../selection-context-menu";
import { Loading } from "../loading";

type ViewerSettings = {
  // number of pixels we scan to set up the virtual list (will change with scale)
  overScan: number;
  // number of pixels between each page
  pageGap: number;
};

type ViewerProps = {
  file: PDFFile;
  options?: Partial<InitialViewerState>;
  password?: string;
  callbacks?: ViewerCallbacks;
  settings?: ViewerSettings;
  onDocumentLoaded?: () => void;
};

export type LayerProps = {
  page: Page;
  scale: number;
  isZooming: boolean;
};

export const worker = createPdfWorker();

export const Viewer: FC<ViewerProps> = ({
  file,
  options = {
    scale: 1.5,
    pageIndex: 0,
  },
  settings = {
    overScan: 1000,
    pageGap: 10,
  },
  password = "",
  onDocumentLoaded,
}) => {
  const [isEngineReady, setEngineReady] = useState(false);
  const ref = useRef<HTMLDivElement>(null);
  const [isLoadingDocument, setLoadingDocument] = useState(true);
  const setPages = useSetAtom(pagesAtom);

  const startingOptions = useMemo(
    () => ({ scale: options.scale ?? 1.5, pageIndex: options.pageIndex ?? 0 }),
    [options]
  );

  // hydrate initial state
  useHydrateAtoms([
    [scaleAtom, startingOptions.scale],
    [pageIndexAtom, startingOptions.pageIndex],
  ]);

  useEffect(() => {
    worker.dispatch({ type: "isReady", payload: {} });
    worker.listen("engineReady", (isReady) => {
      setEngineReady(isReady);
    });
  }, []);

  useEffect(() => {
    if (isEngineReady) {
      worker.dispatch({
        type: "loadDocument",
        payload: { file, pw: password },
      });
      worker.listen("loadDocument", (pages) => {
        setPages(pages);
        setLoadingDocument(false);
        onDocumentLoaded?.();
      });
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [isEngineReady, file, password, setPages]);

  return (
    <div ref={ref} className="pdf-viewer">
      {isLoadingDocument ? (
        <Loading />
      ) : (
        <>
          <VirtualList
            overScan={settings.overScan}
            pageGap={settings.pageGap}
            itemRender={(page, isZooming) => (
              <PdfPage key={page.index} page={page} isZooming={isZooming} />
            )}
          />
          <SelectionContextMenu target={ref.current} />
        </>
      )}
    </div>
  );
};
