import { debounce } from "lodash";
import { useCallback, useLayoutEffect, useMemo, useState } from "react";

type PDFTextSelection = {
  start: { x: number; y: number };
  end: { x: number; y: number };
  collapsed: boolean;
  width: number;
};

export const usePdfTextSelection = (target: HTMLDivElement | null) => {
  const [selection, setSelection] = useState<PDFTextSelection>();

  const handler = useCallback(() => {
    const selection = window.getSelection();
    if (!selection || selection.rangeCount === 0) return;
    const range = selection.getRangeAt(0);
    if (!target || !target.contains(range.commonAncestorContainer)) return;
    const rects = range.getClientRects();
    if (rects.length === 0) return;

    let width = 0;

    Object.values(rects).forEach((rect) => {
      if (width <= rect.width) width = rect.width;
    });
    Object.values(rects).forEach((rect) => {
      if (width <= rect.width) width = rect.width;
    });

    setSelection({
      start: {
        x: rects[0].left,
        y: rects[0].top,
      },
      end: {
        x: rects[rects.length - 1].right,
        y: rects[rects.length - 1].bottom,
      },
      collapsed: range.collapsed,
      width: rects[rects.length - 1].width,
    });
  }, [target]);

  const debouncedHandler = useMemo(
    () => debounce(handler, 300, { leading: false }),
    [handler]
  );

  useLayoutEffect(() => {
    document.addEventListener("selectionchange", debouncedHandler);
    document.addEventListener("keydown", debouncedHandler);
    document.addEventListener("keyup", debouncedHandler);
    window.addEventListener("resize", debouncedHandler);
    window.addEventListener("wheel", debouncedHandler);

    return () => {
      document.removeEventListener("selectionchange", debouncedHandler);
      document.removeEventListener("keydown", debouncedHandler);
      document.removeEventListener("keyup", debouncedHandler);
      window.removeEventListener("resize", debouncedHandler);
      window.removeEventListener("wheel", debouncedHandler);
    };
  }, [debouncedHandler]);

  return selection;
};
