import { Page, SpecialZoom } from "./types";

// coverts an image data to a URL (yes I know it's kinda slow)
export const imageDataToDataUrl = (imageData: ImageData) => {
  const canvas = document.createElement("canvas");
  canvas.width = imageData.width;
  canvas.height = imageData.height;
  const context = canvas.getContext("2d");
  context?.putImageData(imageData, 0, 0);
  return canvas.toDataURL();
};

export const limitZoom = (scale: number) => {
  if (scale <= 0.5) return 0.5;
  if (scale >= 4.0) return 4.0;
  return scale;
};

export const renderImageDataOnCanvas = (
  canvas: HTMLCanvasElement,
  imageData: ImageData
) => {
  canvas.width = imageData.width;
  canvas.height = imageData.height;
  const ctx = canvas.getContext("2d", { alpha: false });
  if (!ctx) return;
  ctx.putImageData(imageData, 0, 0, 0, 0, imageData.width, imageData.height);
};

export const getViewportHeight = (pages: Page[], scale: number, margin = 0) => {
  const len = pages.length;
  let vpH = 0;
  for (let i = 0; i < len; i++) vpH += pages[i].height * scale + margin;
  return vpH;
};

export const isBetween = (value: number, floor: number, ceil: number) =>
  value >= floor && value <= ceil;
export const clamp = (value: number) => Math.max(0, value);

// returns the fixed coords based on the currently 'active' page and its index
export const getPageWithCoords = (
  coords: { x: number; y: number },
  scale: number
) => {
  const pages = document.querySelectorAll("div[data-page-index]");
  let foundIndex = 0;
  let foundPage;
  let fixedCoords;
  let isInPage = false;
  for (let i = 0; i < pages.length; i++) {
    const pageRect = pages[i].getBoundingClientRect();

    const isXinPage = isBetween(coords.x, pageRect.left, pageRect.right);
    const isYinPage = isBetween(coords.y, pageRect.top, pageRect.bottom);

    isInPage = isXinPage && isYinPage;

    if (isInPage) {
      foundPage = pages[i];
      fixedCoords = {
        x: (coords.x - pageRect.left) / scale,
        y: (coords.y - pageRect.top) / scale,
      };
    }
  }

  if (foundPage && fixedCoords) {
    const index = foundPage.getAttribute("data-page-index");
    if (index) foundIndex = parseInt(index);
    return { foundIndex, fixedCoords };
  }
};

export const getPagePositions = (
  pages: Page[],
  pageGap: number,
  scale: number
) => {
  const pagePositions: { index: number; top: number; bottom: number }[] = [];
  let prevBottom = 0;
  let viewportHeight = 0;
  pages.forEach((it, i) => {
    viewportHeight += pages[i].height * scale + pageGap;
    const top = prevBottom + pageGap;
    const bottom = top + it.height * scale;
    prevBottom = bottom;
    pagePositions.push({ index: it.index, top, bottom });
  });
  return { viewportHeight, pagePositions };
};

export const getSpecialZoomScale = (
  specialZoom: SpecialZoom,
  viewerContainer: HTMLDivElement | null,
  pageWidth: number,
  pageHeight: number
) => {
  if (!viewerContainer) return;
  const viewerWidth = viewerContainer.clientWidth - 20;
  const viewerHeight = viewerContainer.clientHeight;
  switch (specialZoom) {
    case SpecialZoom.PageFit:
      return viewerHeight / pageHeight;
    case SpecialZoom.PageWidth:
      return viewerWidth / pageWidth;
    case SpecialZoom.None:
      return;
  }
};
