import { FC } from "react";
import { Page } from "../../core/types";
import { VirtualListProps } from "./virtual-list";

export const ListItem: FC<{
  page: Page;
  itemRender: VirtualListProps["itemRender"];
  scale: number;
  pageGap: number;
  isZooming: boolean;
}> = ({ itemRender, page, scale, pageGap, isZooming }) => {
  return (
    <div
      style={{
        width: "100%",
        position: "absolute",
        top: `${
          page.index * page.height * scale + pageGap * (page.index + 1)
        }px`,
      }}
    >
      {itemRender(page, isZooming)}
    </div>
  );
};
