import { useEffect, useState } from "react";
import { worker } from "../components/viewer/viewer";

export const useThumbnails = (range: { start: number; end: number }) => {
  const [urls, setUrls] = useState<string[]>([]);

  useEffect(() => {
    if (urls.length === 0)
      worker.dispatch({ type: "getThumbnails", payload: range });
  }, [urls, range]);

  useEffect(() => {
    worker.listen("getThumbnails", (urls) => setUrls(urls));
  }, []);

  return urls;
};
