import { FC } from "react";
import { ToolbarButton } from "./toolbar-button";
import { useNavigation } from "../hooks/use-navigation";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faChevronLeft,
  faChevronRight,
} from "@fortawesome/free-solid-svg-icons";
import { useViewerHandlers } from "../hooks/use-viewer-handlers";

export const Navigator: FC = () => {
  const { isFirstPage, isLastPage, pageIndex, pageCount } = useNavigation();
  const handlers = useViewerHandlers();
  return (
    <div className="pdf-navigator">
      <ToolbarButton
        icon={<FontAwesomeIcon icon={faChevronLeft} />}
        onClick={handlers?.prev}
        disabled={isFirstPage()}
      />
      <div className="pdf-navigator-counter">
        <span>
          {pageIndex + 1} of {pageCount}
        </span>
      </div>
      <ToolbarButton
        icon={<FontAwesomeIcon icon={faChevronRight} />}
        onClick={handlers?.next}
        disabled={isLastPage()}
      />
    </div>
  );
};
