import {
  FC,
  PropsWithChildren,
  useCallback,
  useEffect,
  useMemo,
  useRef,
} from "react";
import {
  getPagePositions,
  getSpecialZoomScale,
  isBetween,
} from "../../core/utils";
import { useAtom, useAtomValue, useSetAtom } from "jotai";
import {
  pageIndexAtom,
  pagesAtom,
  scaleAtom,
  specialZoomAtom,
  viewerElementAtom,
} from "../../atoms";
import { SpecialZoom } from "../../core/types";
import { isEqual } from "lodash";

type VirtualScrollerProps = {
  pageGap: number;
  overScan: number;
  isZooming?: (zooming: boolean) => void;
  onRangeChange?: (range: { startIndex: number; endIndex: number }) => void;
};

export const VirtualScroller: FC<PropsWithChildren<VirtualScrollerProps>> = ({
  children,
  pageGap,
  overScan,
  isZooming,
  onRangeChange,
}) => {
  const pages = useAtomValue(pagesAtom);
  const [scale, setScale] = useAtom(scaleAtom);
  const setPageIndex = useSetAtom(pageIndexAtom);
  const specialZoom = useAtomValue(specialZoomAtom);
  const listRef = useRef<HTMLDivElement>(null);
  const wheelTimer = useRef<NodeJS.Timer>();
  const prevRange = useRef<
    { startIndex: number; endIndex: number } | undefined
  >();
  const setViewerElement = useSetAtom(viewerElementAtom);

  useEffect(() => {
    if (listRef.current) setViewerElement(listRef.current);
  }, [setViewerElement]);

  const { viewportHeight, pagePositions } = useMemo(
    () => getPagePositions(pages, pageGap, scale),
    [pages, pageGap, scale]
  );

  const itemHeight = useMemo(() => {
    if (pages.length === 0) return 0;
    return pages[0].height * scale;
  }, [pages, scale]);

  const getPageRange = useCallback(
    (scrollTop: number) => {
      if (pages.length === 0 || !listRef.current) return;
      const start = Math.floor(scrollTop / (itemHeight + 15));
      const startIndex = start > 0 ? start - 1 : 0;
      const endIndex = Math.min(
        startIndex +
          Math.ceil((listRef.current.clientHeight + overScan) / itemHeight) +
          1,
        pages.length
      );
      return { startIndex, endIndex };
    },
    [itemHeight, overScan, pages]
  );

  // TODO: we should move this somewhere else
  const onResize = useCallback(() => {
    if (specialZoom !== SpecialZoom.None) {
      const realScale = getSpecialZoomScale(
        specialZoom,
        listRef.current,
        pages[0].width,
        pages[0].height
      );
      if (realScale) setScale(realScale);
    }
  }, [pages, setScale, specialZoom]);

  // TODO: we should move this somewhere else
  useEffect(() => {
    window.addEventListener("resize", onResize);
    return () => {
      window.removeEventListener("resize", onResize);
    };
  }, [onResize]);

  useEffect(() => {
    const range = getPageRange(0);
    if (onRangeChange && range) {
      onRangeChange(range);
      prevRange.current = range;
    }
    //* setting the correct deps causes the this function to be called when zooming
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [onRangeChange, pages]);

  const handleScroll = useCallback(() => {
    if (listRef.current) {
      const scrollHeight = listRef.current.scrollHeight;
      const percentageScroll =
        listRef.current.scrollTop /
        (scrollHeight - listRef.current.clientHeight);
      const page = pagePositions.find((it) =>
        isBetween(percentageScroll * scrollHeight, it.top, it.bottom)
      );
      if (page) setPageIndex(page.index);
      const newRange = getPageRange(listRef.current.scrollTop);
      // TODO: isEqual worth it? or is it too slow?
      if (newRange && !isEqual(newRange, prevRange.current)) {
        onRangeChange?.(newRange);
        prevRange.current = newRange;
      }
    }
  }, [pagePositions, setPageIndex, getPageRange, onRangeChange]);

  const handleWheel = useCallback(
    (e: WheelEvent) => {
      const unInitializedButton = e.buttons === 0;
      if ((e.altKey || e.ctrlKey || e.metaKey) && unInitializedButton) {
        if (wheelTimer.current) {
          clearTimeout(wheelTimer.current);
          isZooming?.(true);
        }
        e.preventDefault();
        e.stopPropagation();
        //! this algorithm is just weird find a better one
        const signY = Math.sign(e.deltaY);
        const absDeltaY = Math.abs(e.deltaY);
        let normalizedDeltaY = e.deltaY;
        const ZOOM_MAX_STEP = 10;
        if (absDeltaY > ZOOM_MAX_STEP) {
          normalizedDeltaY = ZOOM_MAX_STEP * signY;
        }

        setScale((currentScale) => {
          const prevScale = currentScale;
          const newScale = currentScale * 0.99 ** normalizedDeltaY;
          if (listRef.current && prevScale !== 4.0 && prevScale !== 0.5) {
            const scaleDiff = newScale / prevScale;
            if (scaleDiff !== 0) {
              // maintains scrollTop & scrollLeft relative pos
              listRef.current.scrollLeft *= scaleDiff;
              listRef.current.scrollTop *= scaleDiff;
            }
          }
          return newScale;
        });

        wheelTimer.current = setTimeout(() => {
          isZooming?.(false);
          wheelTimer.current = undefined;
        }, 300);
      }
    },
    [isZooming, setScale]
  );

  useEffect(() => {
    const el = listRef.current;
    if (!el) return;
    el.addEventListener("wheel", handleWheel);
    el.addEventListener("scroll", handleScroll);
    return () => {
      el.removeEventListener("wheel", handleWheel);
      el.removeEventListener("scroll", handleScroll);
    };
  }, [handleWheel, handleScroll]);

  return (
    <div
      ref={listRef}
      style={{
        height: "100%",
        width: "100%",
        overflow: "auto",
        position: "relative",
      }}
    >
      <div
        style={{
          height: `${viewportHeight}px`,
          paddingBottom: `20px`,
        }}
      >
        {children}
      </div>
    </div>
  );
};
