import { FC, PropsWithChildren, useEffect, useMemo, useRef } from "react";
import { createPortal } from "react-dom";
import { usePdfTextSelection } from "../hooks/use-pdf-text-selection";
import { getPageWithCoords } from "../core/utils";
import { worker } from "./viewer/viewer";
import { useAtomValue } from "jotai";
import { scaleAtom } from "../atoms";
import { faCopy, faHighlighter } from "@fortawesome/free-solid-svg-icons";
import { ToolbarButton } from "./toolbar-button";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

const Portal: FC<PropsWithChildren> = ({ children }) => {
  return createPortal(children, document.body);
};

export const SelectionContextMenu: FC<{
  target: HTMLDivElement | null;
}> = ({ target }) => {
  const selection = usePdfTextSelection(target);
  const scale = useAtomValue(scaleAtom);
  const copiedText = useRef("");

  const fixedSelection = useMemo(() => {
    if (selection && !selection.collapsed) {
      return {
        start: getPageWithCoords(selection.start, scale),
        end: getPageWithCoords(selection.end, scale),
      };
    }
  }, [scale, selection]);

  useEffect(() => {
    if (fixedSelection && fixedSelection.start && fixedSelection.end) {
      worker.dispatch({
        type: "selectedText",
        payload: {
          start: {
            pageIndex: fixedSelection.start.foundIndex,
            coord: fixedSelection.start.fixedCoords,
          },
          end: {
            pageIndex: fixedSelection.end.foundIndex,
            coord: fixedSelection.end.fixedCoords,
          },
        },
      });
    }
  }, [fixedSelection]);

  useEffect(() => {
    worker.listen("selectedText", (text) => {
      copiedText.current = text;
    });
  }, []);

  useEffect(() => {
    const handleKeyDown = async (event: KeyboardEvent) => {
      const isModifierPressed = event.ctrlKey || event.metaKey;
      if (isModifierPressed && event.code === "KeyC") {
        event.preventDefault();
        try {
          await navigator.clipboard.writeText(copiedText.current);
          console.log("Content copied to clipboard");
        } catch (err) {
          console.error("Failed to copy: ", err);
        }
        console.log("Copied text: ", copiedText.current);
      }
    };

    window.addEventListener("keydown", handleKeyDown);

    return () => window.removeEventListener("keydown", handleKeyDown);
  }, []);

  if (!selection || !target || selection.collapsed) return null;

  return (
    <Portal>
      <div
        className="pdf-text-selection-overlay"
        style={{
          left: `${selection.end.x}px`,
          top: `${selection.end.y}px`,
        }}
      >
        <button
          className="toolbar-button"
          onClick={async (e) => {
            e.preventDefault();
            try {
              await navigator.clipboard.writeText(copiedText.current);
              console.log("Content copied to clipboard: ", copiedText.current);
            } catch (err) {
              console.error("Failed to copy: ", err);
            }
          }}
        >
          <FontAwesomeIcon icon={faCopy} />
        </button>
        <ToolbarButton
          onClick={() => {
            console.log("Highlight!");
          }}
          icon={<FontAwesomeIcon icon={faHighlighter} />}
        />
      </div>
    </Portal>
  );
};
