import { atom } from "jotai";

import { GroupMode, Page, ScrollMode, SpecialZoom } from "../core/types";
import { limitZoom } from "../core/utils";

export const pagesAtom = atom<Page[]>([]);
export const scaleAtom = atom<
  number,
  [number | ((prev: number) => number)],
  void
>(1.5, (get, set, scale) => {
  if (typeof scale === "number") {
    set(scaleAtom, limitZoom(Math.round(scale * 100) / 100));
  } else {
    set(scaleAtom, limitZoom(Math.round(scale(get(scaleAtom)) * 100) / 100));
  }
});

export const pageIndexAtom = atom<
  number,
  [number | ((currentIndex: number) => number)],
  void
>(0, (get, set, index) => {
  let newIndex;
  if (typeof index === "number") {
    newIndex = index;
  } else {
    newIndex = index(get(pageIndexAtom));
  }
  const isOutOfBounds = newIndex < 0 || newIndex >= get(pagesAtom).length;
  const isSame = newIndex === get(pageIndexAtom);
  if (!isOutOfBounds && !isSame) set(pageIndexAtom, index);
});

export const groupModeAtom = atom(GroupMode.SinglePage);
export const scrollModeAtom = atom(ScrollMode.Vertical);
export const specialZoomAtom = atom(SpecialZoom.None);
export const viewerElementAtom = atom<HTMLDivElement | undefined>(undefined);
