import React, { FC, useEffect, useState } from "react";
import { Viewer } from "./components/viewer/viewer";
import { PDFFile } from "./core/types";
import { Navigator } from "./components/navigator";
import { Toolbar } from "./components/toolbar";
import { Sidebar } from "./components/sidebar";

const pdfs = ["test.pdf", "test2.pdf", "test3.pdf", "test4.pdf"];

const App: FC = () => {
  const [url, setUrl] = useState<string>("test2.pdf");
  const [pdf, setPdf] = useState<PDFFile | undefined>();
  const [isSidebarOpen, setSidebarOpen] = useState(false);
  const [isDocLoaded, setIsDocLoaded] = useState(false);

  useEffect(() => {
    const fetchDoc = async () => {
      const file = await fetch(url);
      const arrayBuffer = await file.arrayBuffer();
      setPdf({
        id: "1",
        name: "test.pdf",
        content: arrayBuffer,
      });
    };
    fetchDoc();
  }, [url]);

  if (!pdf) return <></>;

  return (
    <div className="App">
      <div className="pdf-viewer-layout">
        <div className="pdf-viewer-container">
          <Toolbar
            isSidebarOpen={isSidebarOpen}
            toggleSidebar={() => setSidebarOpen((s) => !s)}
          />
          <div
            style={{
              display: "flex",
              justifyContent: "flex-start",
              width: "100%",
              height: "100%",
            }}
          >
            {isSidebarOpen && <Sidebar />}
            <div
              style={{
                width: "100%",
                height: "100%",
                position: "relative",
              }}
            >
              <Viewer
                file={pdf}
                options={{
                  scale: 1.5,
                  pageIndex: 2,
                }}
                onDocumentLoaded={() => {
                  setIsDocLoaded(true);
                }}
              />
              {isDocLoaded && <Navigator />}
            </div>
          </div>
        </div>
      </div>

      <div
        style={{
          position: "fixed",
          bottom: 0,
          left: 0,
          zIndex: 9999,
        }}
      >
        <span>Select PDF from dropdown</span>
        <select
          value={url}
          onChange={(e) => {
            setUrl(e.target.value);
          }}
        >
          {pdfs.map((it) => (
            <option key={it} value={it}>
              {it}
            </option>
          ))}
        </select>
      </div>
    </div>
  );
};

export default App;
