import { FC, useMemo } from "react";
import { useThumbnails } from "../hooks/use-thumbnails";
import { useAtomValue } from "jotai";
import { pageIndexAtom } from "../atoms";
import { useViewerHandlers } from "../hooks/use-viewer-handlers";

const ThumbnailList: FC = () => {
  const urls = useThumbnails({ start: 0, end: 13 });
  const pageIndex = useAtomValue(pageIndexAtom);
  const handlers = useViewerHandlers();
  const isLoading = useMemo(() => urls.length === 0, [urls]);

  return (
    <div
      style={{
        height: "100%",
        width: "100%",
      }}
    >
      <div
        style={{
          display: "flex",
          justifyContent: "flex-start",
          alignItems: "center",
          flexDirection: "column",
          paddingBottom: "100px",
          paddingTop: "10px",
        }}
      >
        {isLoading && <>loading</>}
        {urls.map((url, i) => (
          <div
            key={`thumbnail-${i}`}
            onClick={() => handlers?.goToPage(i)}
            style={{
              marginBottom: "10px",
              borderRadius: "6px",
              background: pageIndex === i ? "#ccc" : "transparent",
              cursor: pageIndex === i ? "auto" : "pointer",
            }}
          >
            <img
              className="thumbnail"
              src={url}
              alt={`Thumbnail page ${i + 1}`}
            />
            <div style={{ textAlign: "center", fontSize: "14px" }}>
              <span>{i + 1}</span>
            </div>
          </div>
        ))}
      </div>
    </div>
  );
};

export const Sidebar: FC = () => {
  return (
    <div className="sidebar">
      <ThumbnailList />
    </div>
  );
};
