import { EngineRequest, EngineResult, EngineRunnerResult } from "./test.worker";
export const createPdfWorker = () => {
  const worker = new Worker(new URL("./test.worker.ts", import.meta.url));

  const listen = <T extends keyof EngineRunnerResult>(
    ev: T,
    cb: (payload: EngineRunnerResult[T]) => void
  ) => {
    worker.addEventListener(
      "message",
      (message: MessageEvent<EngineResult>) => {
        if (ev === message.data.type) {
          cb(message.data.payload as EngineRunnerResult[typeof ev]);
        }
      }
    );
  };
  const dispatch = (req: EngineRequest) => {
    worker.postMessage(req);
  };

  const removeListener = () => worker.removeEventListener("message", () => {});

  return { listen, dispatch, removeListener };
};
