import { FC } from "react";
import { ToolbarButton, ToolbarSelect } from "./toolbar-button";
import { useAtomValue } from "jotai";
import { scaleAtom, specialZoomAtom } from "../atoms";
import { SpecialZoom } from "../core/types";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faPlus,
  faMinus,
  faBars,
  faTimes,
} from "@fortawesome/free-solid-svg-icons";
import { useViewerHandlers } from "../hooks/use-viewer-handlers";

type ZoomSectionProps = {
  zoomToSpecialValue?: (scale: SpecialZoom) => void;
  zoom?: (scale: number) => void;
  zoomIn?: () => void;
  zoomOut?: () => void;
};

export const ZoomSection: FC<ZoomSectionProps> = ({
  zoomToSpecialValue,
  zoomIn,
  zoomOut,
}) => {
  const scale = useAtomValue(scaleAtom);
  const specialZoom = useAtomValue(specialZoomAtom);
  return (
    <div className="zoom">
      <ToolbarButton
        icon={<FontAwesomeIcon icon={faPlus} />}
        onClick={zoomIn}
      />
      {Math.round(scale * 100)}%
      <ToolbarButton
        icon={<FontAwesomeIcon icon={faMinus} />}
        onClick={zoomOut}
      />
      <ToolbarSelect
        value={specialZoom}
        options={[
          { label: "Page width", value: SpecialZoom.PageWidth },
          { label: "Page fit", value: SpecialZoom.PageFit },
          { label: "None", value: SpecialZoom.None },
        ]}
        onChange={(v) => {
          zoomToSpecialValue?.(v as SpecialZoom);
        }}
      />
    </div>
  );
};

type ToolbarProps = {
  pdfName?: string;
  isSidebarOpen: boolean;
  toggleSidebar: () => void;
};

export const Toolbar: FC<ToolbarProps> = ({
  pdfName,
  isSidebarOpen,
  toggleSidebar,
}) => {
  const handlers = useViewerHandlers();
  return (
    <div className="toolbar-container">
      <ToolbarButton
        icon={<FontAwesomeIcon icon={isSidebarOpen ? faTimes : faBars} />}
        onClick={toggleSidebar}
      />
      {pdfName && <h1>{pdfName}</h1>}
      <ZoomSection
        zoom={handlers?.zoom}
        zoomIn={handlers?.zoomIn}
        zoomOut={handlers?.zoomOut}
        zoomToSpecialValue={handlers?.zoomToSpecialValue}
      />
    </div>
  );
};
