import { FC, memo, useEffect, useMemo, useRef, useState } from "react";
import { Page, PageStatus } from "../../core/types";
import { Loading } from "../loading";
import { TextLayer } from "./text-layer";
import { renderImageDataOnCanvas } from "../../core/utils";
import { useAtomValue } from "jotai";
import { scaleAtom } from "../../atoms";
import { worker } from "./viewer";

type PdfPageProps = {
  page: Page;
  isZooming: boolean;
};

const MemoTextLayer = memo(
  TextLayer,
  (prev, next) =>
    prev.page.index === next.page.index && prev.scale === next.scale
);

export const PdfPage: FC<PdfPageProps> = ({ page, isZooming }) => {
  const scale = useAtomValue(scaleAtom);
  const canvasRef = useRef<HTMLCanvasElement>(null);
  const pageRef = useRef<HTMLDivElement>(null);
  const prevPageStatus = useRef<PageStatus>({ rendered: false, scale });
  const [pageStatus, setPageStatus] = useState<PageStatus>(
    prevPageStatus.current
  );

  useEffect(() => {
    worker.listen("pageImageData", ({ imageData, index, renderedScale }) => {
      if (index === page.index) {
        if (canvasRef.current && imageData) {
          // prevent re-rendering the canvas if the the page
          // was already loaded in a previous render OR if the container is scrolling
          if (
            !prevPageStatus.current.rendered ||
            prevPageStatus.current.scale !== renderedScale
          ) {
            renderImageDataOnCanvas(canvasRef.current, imageData);
          }
          setPageStatus({ rendered: true, scale: renderedScale });
          prevPageStatus.current = { rendered: true, scale: renderedScale };
        }
      }
    });
  }, [page.index]);

  const isLoading = useMemo(
    () => !pageStatus.rendered && !prevPageStatus.current.rendered,
    [pageStatus]
  );

  return (
    <div
      ref={pageRef}
      data-page-index={page.index}
      className="page-container"
      style={{
        width: Math.ceil(page.width * scale),
        height: Math.ceil(page.height * scale),
      }}
    >
      <canvas
        className="render-layer"
        style={{ background: "#fff" }}
        ref={canvasRef}
      />
      <MemoTextLayer page={page} scale={scale} isZooming={isZooming} />
      {isLoading && <Loading />}
    </div>
  );
};
