import { FSMatrix } from "./geometry";

export enum BitmapFormat {
  Bitmap_Gray = 1,
  Bitmap_BGR = 2,
  Bitmap_BGRx = 3,
  Bitmap_BGRA = 4,
}

export enum RenderFlag {
  ANNOT = 0x01, // Set if annotations are to be rendered.
  LCD_TEXT = 0x02, // Set if using text rendering optimized for LCD display.
  NO_NATIVETEXT = 0x04, // Don't use the native text output available on some platforms
  GRAYSCALE = 0x08, // Grayscale output.
  DEBUG_INFO = 0x80, // Set if you want to get some debug info. Please discuss with Foxit first if you need to collect debug info.
  NO_CATCH = 0x100, // Set if you don't want to catch exception.
  RENDER_LIMITEDIMAGECACHE = 0x200, // Limit image cache size.
  RENDER_FORCEHALFTONE = 0x400, // Always use halftone for image stretching.
  PRINTING = 0x800, // Render for printing.
  REVERSE_BYTE_ORDER = 0x10, // Set whether render in a reverse Byte order, this flag only.
}

export type PDFTextRect = {
  text: string;
  rect: PDFRect;
  transformX: number;
  transformY: number;
};

export type PDFRect = {
  top: number;
  left: number;
  right: number;
  bottom: number;
  x: number;
  y: number;
  width: number;
  height: number;
};

export type PDFMetadata = {
  title: string;
  author: string;
  subject: string;
  keywords: string;
  producer: string;
  creator: string;
  creationDate: string;
  modificationDate: string;
};

export type PDFFile = {
  id: string;
  name: string;
  content: ArrayBuffer;
};

export enum Rotation {
  Degree0 = 0,
  Degree90 = 1,
  Degree180 = 2,
  Degree270 = 3,
}

export enum MatchFlag {
  None = 0,
  MatchCase = 1,
  MatchWholeWord = 2,
  MatchConsecutive = 4,
}

export interface SearchTarget {
  keyword: string;
  // flags: MatchFlag[];
  flag: MatchFlag;
}

export interface SearchContext {
  target: SearchTarget;
  currPageIndex: number;
  startIndex: number; // -1 means reach the end
}

export type FontData = {
  fontSize: number;
  fontFamily: string;
  fsMatrix: FSMatrix;
};

export type TextSelection = {
  startPageIndex: number;
  endPageIndex: number;
  startCharIndex: number;
  endCharIndex: number;
};

export type CharBounds = {
  start: number;
  offset: number;
};

export type PageTextSelection = {
  start: number;
  end: number;
  index: number;
};

export type Page = {
  width: number;
  height: number;
  docPtr: number;
  index: number;
};

export enum GroupMode {
  SinglePage = "single-page",
  DualPage = "dual-page",
}

export enum ScrollMode {
  Vertical = "vertical",
  Horizontal = "horizontal",
}

export enum SpecialZoom {
  PageFit = "page-fit",
  PageWidth = "page-width",
  None = "none",
}

export type InitialViewerState = {
  scale: number;
  pageIndex: number;
};

export type WorkerInputPayloads = {
  LOAD_DOCUMENT: { file: PDFFile; password: string };
  RENDER_PAGE: { page: Page; scale: number };
  GET_SELECTED_TEXT: {
    startPageIndex: number;
    endPageIndex: number;
    startCoords: Coords;
    endCoords: Coords;
  };
  GET_THUMBNAILS: Page[];
  GET_METADATA: undefined;
  SEARCH_TEXT: SearchTarget;
};

export type WorkerOutputPayloads = {
  INIT_PDFIUM: undefined;
  PAGES: Page[];
  IMAGEDATA: { imageData: ImageData; renderedScale: number; index: number };
  TEXT_RECTS: { index: number; textRects: PDFTextRect[] };
  SELECTED_TEXT: string;
  THUMBNAILS: string[];
  METADATA: PDFMetadata;
  SEARCHED_TEXT: {
    pageIndex: number;
    charIndex: number;
    charCount: number;
  }[];
};

export type WorkerInputKeys = keyof Required<WorkerInputPayloads>;
export type WorkerOutputKeys = keyof Required<WorkerOutputPayloads>;

export type WorkerRequest = {
  [T in WorkerInputKeys]: { type: T; payload: WorkerInputPayloads[T] };
}[WorkerInputKeys];

export type WorkerResult = {
  [T in WorkerOutputKeys]: { type: T; payload: WorkerOutputPayloads[T] };
}[WorkerOutputKeys];

export type PageStatus = {
  rendered: boolean;
  scale: number;
};

export type Coords = {
  x: number;
  y: number;
};

export type SearchResult = {
  occurrences: number;
  pageIndex: number;
};

export type ViewerCallbacks = {
  onSearch: (result: SearchResult[] | undefined) => void;
  onPageRender: (indexes: number[]) => void;
};

export type ViewerHandlers = {
  // scrolls to page by index
  goToPage: (index: number) => void;
  // scrolls to next page by index
  next: () => void;
  // scrolls to prev page by index
  prev: () => void;
  // zoom to special scale
  zoomToSpecialValue: (scale: SpecialZoom) => void;
  // zoom to scale
  zoom: (scale: number) => void;
  // zoom in
  zoomIn: () => void;
  // zoom out
  zoomOut: () => void;
  // gets thumbnails for the given indexes
  // getThumbnails: (startIndex: number, endIndex: number) => void;
  // gets metadata
  // getMetadata: () => void;
  // search word starting from TOP of pdf & scrolls to first occurrence
  // findNext: (keyword: string, flag: MatchFlag) => void;
  // search word starting from BOTTOM of pdf & scrolls to first occurrence
  // findLast: (keyword: string, flag: MatchFlag) => void;
};
