import { FC, ReactNode, memo, useCallback, useState } from "react";
import { Page } from "../../core/types";
import { useAtomValue } from "jotai";
import { pagesAtom, scaleAtom } from "../../atoms";
import { ListItem } from "./virtual-list-item";
import { VirtualScroller } from "./virtual-scroller";
import { worker } from "./viewer";
import { useDebouncedScale } from "../../hooks/use-debounced-scale";

export type VirtualListProps = {
  overScan?: number;
  pageGap?: number;
  itemRender: (page: Page, isZooming: boolean) => ReactNode;
};

const MemoListItem = memo(
  ListItem,
  (prev, next) =>
    prev.page.index === next.page.index &&
    prev.scale === next.scale &&
    prev.isZooming === next.isZooming
);

export const VirtualList: FC<VirtualListProps> = ({
  itemRender,
  overScan = 1000,
  pageGap = 10,
}) => {
  const pages = useAtomValue(pagesAtom);
  const [pageRange, setPageRange] = useState<Page[]>([]);
  const [isZooming, setIsZooming] = useState(false);
  const scale = useAtomValue(scaleAtom);
  const debouncedScale = useDebouncedScale();

  const onRangeChange = useCallback(
    (range: { startIndex: number; endIndex: number }) => {
      const toRender = pages.slice(range.startIndex, range.endIndex);
      worker.dispatch({
        type: "renderPages",
        payload: { pages: toRender, scale: debouncedScale },
      });
      setPageRange(pages.slice(range.startIndex, range.endIndex));
    },
    [debouncedScale, pages]
  );

  const onZoomingChange = useCallback((isZooming: boolean) => {
    setIsZooming(isZooming);
  }, []);

  return (
    <VirtualScroller
      pageGap={pageGap}
      overScan={overScan * scale}
      isZooming={onZoomingChange}
      onRangeChange={onRangeChange}
    >
      {pageRange.map((page, i) => (
        <MemoListItem
          key={page.index}
          itemRender={itemRender}
          page={page}
          scale={scale}
          pageGap={pageGap}
          isZooming={isZooming}
        />
      ))}
    </VirtualScroller>
  );
};
