import { FC, Fragment, useEffect, useMemo, useRef, useState } from "react";
import { PDFTextRect } from "../../core/types";
import { LayerProps, worker } from "./viewer";

export const TextLayer: FC<LayerProps> = ({ page, scale, isZooming }) => {
  const textLayerRef = useRef<HTMLDivElement>(null);
  const [rects, setRects] = useState<PDFTextRect[]>([]);

  useEffect(() => {
    worker.listen("pageTextRects", ({ index, textRects }) => {
      if (index === page.index && !isZooming) {
        setRects(textRects);
      }
    });
  }, [isZooming, page.index]);

  const rectsToRender = useMemo(() => {
    return rects.map(({ rect, text, transformX, transformY }, index) => {
      if (rect.x === undefined || rect.y === undefined)
        return <Fragment key={index} />;
      return (
        <span
          key={index}
          className="text-rect"
          style={{
            top: rect.y,
            left: rect.x,
            transform: `scale(${transformX}, ${transformY})`,
          }}
        >
          {text}
        </span>
      );
    });
  }, [rects]);

  return (
    <div
      ref={textLayerRef}
      data-layer-type="text-layer"
      className="page-layer"
      style={{
        width: page.width,
        height: page.height,
        transform: `scale(${scale}, ${scale})`,
        transformOrigin: "0 0 0",
      }}
    >
      {rectsToRender}
    </div>
  );
};
